
.flex {
  display: flex;
  flex-direction: column;
  position: relative;
}

.hero_heading {
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-40%, -50%);
}

.hero_heading h1{
  font-family: 'Poppins';
  font-weight: bold;
  font-size: 50px;
  color: white;
  text-shadow: 2px 2px black;
}


@media screen and (max-width: 920px) {
  .hero_heading {
    position: absolute;
    justify-content: center;
    transform: translate(-40%, -50%);
  }
  .hero_heading h1 {
      font-size: 25px;
  }
}
